.Joke{
  display: flex;
  border-bottom: 2px solid #eeeeee;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  padding: 1rem;
}

.Joke-Button {
  display: flex;
  justify-content: center;
  align-self: center;
  margin-right: 1rem;
  width: 15%;
}

.Joke-Text{
  width: 75%;
  font-size: 1.2rem;
}

.Joke-Votes {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  text-align: center;
  font-size: 20px;
  font-weight: 300;
  border: 3px solid red;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.fa-arrow-up,
.fa-arrow-down {
  font-size: 1.5rem;
  margin: 10px;
  cursor: pointer;
  opacity: 0.85;
  transition: 0.3s ease;
}

.fa-arrow-up:hover,
.fa-arrow-down:hover {
  opacity: 1;
  filter: brightness(0.5);
}

.Joke-Emoji {
  font-size: 2rem;
  margin-left: auto;
  border-radius: 50%;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}